<template>
  <footer>
    <router-link
      v-for="(item, index) in menu"
      :key="index"
      :to="item.url"
      :class="[active==item.id?item.id:'']"
      @click.native="tabClick(item.id)"
      :id="item.id"
    >
      <i class="iconfont" :class="item.icon"></i>
      <span class="bthree">{{item.title}}</span>
    </router-link>
  </footer>
</template>

<script>
import base from "../assets/js/base.js";
export default {
  name: "footer-bar",
  data() {
    return {
      active: "",
      menu: [
        { title: "首页", url: "/home", id: "home", icon: "iconshouye" },
        { title: "订单", url: "/order", id: "order", icon: "iconsingle" },
        { title: "我的", url: "/mine", id: "mine", icon: "iconwode" }
      ]
    };
  },
  methods: {
    /***切换tabbar***/
    tabClick(val) {
      this.active = val;
    }
  },
  watch: {
    footerValue(val) {
      this.active = val;
      this.$store.commit("changeActive", val);
    }
  },
  computed: {
    footerValue() {
      return this.$store.state.active;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../assets/scss/base.scss";
footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  align-items: center;
  height: ptr(60px);
  font-size: ptr(14px);
  background-color: #fff;
  box-shadow: 0 3px 14px 2px rgba(0, 0, 0, 0.12);
}

footer a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 33%;
  height: 100%;
  color: #949494;
}
.mine,
.home,
.order {
  color: #0cb0b5;
}
footer i {
  height: ptr(35px);
  width: ptr(35px);
  text-align: center;
  line-height: ptr(35px);
  font-size: ptr(28px) !important;
}
</style>



