<template>
  <div class="order">
    <header-bar>订单详情</header-bar>
    <main>
      <ul class="order-box">
        <li class="clearfix">
          <section class="order-header">
            <span>
              <i class="iconfont iconweibiaoti-"></i>
              {{orderInfo.mallOrder.mallNo}}
            </span>
            <span
              class="order-status"
              v-if="orderInfo.mallOrder.orderStatus==0 && orderInfo.mallOrder.shippingStatus==0"
            >已配送</span>
            <span
              class="order-status"
              v-else-if="orderInfo.mallOrder.orderStatus==0 && orderInfo.mallOrder.shippingStatus==3"
            >已自提</span>
            <span
              class="order-status"
              v-else-if="orderInfo.mallOrder.orderStatus==1 && orderInfo.mallOrder.shippingStatus==1 && orderInfo.mrPictures.length>0"
            >未配送</span>
            <span
              class="order-status"
              v-else-if="orderInfo.mallOrder.orderStatus==1 && orderInfo.mallOrder.shippingStatus==2"
            >未自提</span>
            <span
              class="order-status"
              v-else-if="orderInfo.mallOrder.orderStatus==1 && orderInfo.mrPictures.length<=0"
            >未上传病例</span>
          </section>
          <div class="goods-img">
            <img :src="orderInfo.medItemQr[0].drugSet.titleImg" alt="">
          </div>
          <div class="goods-info">
            <div class="goods-name">{{orderInfo.medItemQr[0].drugSet.commonName}}</div>
            <div class="goods-message">{{orderInfo.medItemQr[0].drugSet.describe}}</div>
            <div class="goods-price">
              <i>￥</i>
              <span>{{orderInfo.medItemQr[0].saleGenPrice}}</span>
              <span class="num">x{{orderInfo.medItemQr[0].number}}</span>
            </div>
          </div>
          <section class="option">
            <span>配送方式</span>
            <span>{{orderInfo.mallOrder.shippingStatus | shippingFilter}}</span>
          </section>
          <section class="option">
            <span>运费</span>
            <span
              class="freight"
              v-if="(orderInfo.mallOrder.shippingStatus==1 || orderInfo.mallOrder.shippingStatus==0) && orderInfo.medItemQr[0].number<3"
            >12</span>
            <span class="freight" v-else>0</span>
          </section>
          <section class="order-sum">
            <span>共{{orderInfo.medItemQr[0].number}}件商品 合计:￥</span>
            <span class="price">{{orderInfo.mallOrder.orderAmount}}</span>
          </section>
        </li>
      </ul>
      <div class="express" v-if="orderInfo.mallOrder.shippingStatus==1 || orderInfo.mallOrder.shippingStatus==0">
        <h2>快递信息</h2>
        <section>
          <span>快递公司:</span>
          <span>{{orderInfo.mallOrder.shippingCompany?orderInfo.mallOrder.shippingCompany:'暂无'}}</span>
        </section>
        <section>
          <span>快递单号:</span>
          <span>
            {{orderInfo.mallOrder.shippingNo?orderInfo.mallOrder.shippingNo:'暂无'}}
            <i
              class="iconfont iconcopy"
              v-clipboard:copy="orderInfo.mallOrder.shippingNo"
              v-clipboard:success="onCopy"
              v-clipboard:error="onError"
            ></i>
          </span>
        </section>
        <section>
          <span>联系人:</span>
          <span>{{orderInfo.receiptAddress.name}}</span>
        </section>
        <section>
          <span>地址:</span>
          <span>{{orderInfo.receiptAddress.province}}{{orderInfo.receiptAddress.city}}{{orderInfo.receiptAddress.county}}{{orderInfo.receiptAddress.addressDetail}}</span>
        </section>
        <section>
          <span>手机号:</span>
          <span>{{orderInfo.receiptAddress.phone}}</span>
        </section>
      </div>
      <div class="shop" v-else>
        <h2>附近门店</h2>
        <ul class="list">
          <li v-for="(item, index) in shopList" :key="index">
            <section>
              <span>药店名称:</span>
              <span>{{item.drugStoreName}}</span>
            </section>
            <section>
              <span>地址:</span>
              <span>{{item.address}}</span>
            </section>
            <section>
              <span>联系方式:</span>
              <span>{{item.telePhone}}</span>
            </section>
          </li>
          <div class="nodata" v-if="isNo">暂无数据</div>
          <div class="location" @click="getCity" v-if="isLocation">获取定位</div>
        </ul>
      </div>
      <div class="fill"></div>
    </main>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import headerBar from "@/components/header-bar";
export default {
  name: "orderDetail",
  data() {
    return {
      orderInfo: {},
      shopList: [],
      isNo: false,
      isLocation:false
    };
  },
  methods: {
    //计算总价
    sumFun() {
      this.sum = base
        .accMul(this.obj.orderAmount, this.obj.prescriptionNum)
        .toFixed(2);
    },
    //复制
    onCopy: function(e) {},
    onError: function(e) {
      base.toast("复制失败");
    },
    //获取药店
    getShop() {
      if (!sessionStorage.getItem("location")) {
        this.isLocation=true;
        return;
      }else{
        this.isLocation=false;
      }
      base
        .postRequest("/drugStore/findDrugStore", {
          address: sessionStorage.getItem("location"),
          type:1,
          page:1,
          rows:999
        })
        .then(res => {
          if (res.status == 0) {
            for(let i=0;i<res.data.list.length;i++){
                res.data.list[i].address=res.data.list[i].address.replace(',','');
            }
            this.shopList = res.data.list;
            if (res.data.list.length > 0) {
              this.isNo = false;
            } else {
              this.isNo = true;
            }
          } else {
            base.toast(res.msg);
          }
        });
    },
    //腾讯地图
    getCity() {
      base.loading();
      var geolocation = new qq.maps.Geolocation();
      geolocation.getIpLocation(e => {
        sessionStorage.setItem("location", e.province +','+ e.city);
        base.closeLoading();
        setTimeout(() => {
          this.getShop();
        }, 100);
      });
    }
  },
  created() {
    this.orderInfo = JSON.parse(sessionStorage.getItem("orderInfo"));
    if (this.orderInfo.mallOrder.shippingStatus == 2 || this.orderInfo.mallOrder.shippingStatus == 3) {
       this.getShop();
     }
  },
  components: {
    "header-bar": headerBar
  },
  filters: {
    shippingFilter(e) {
      if (e == 1 || e==0) {
        return "普通快递";
      } else {
        return "门店自提";
      }
    }
  },
  mounted() {
    this.$store.commit("changeActive", "order");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";

main {
  position: relative;
  top: ptr(44px);
  .order-box {
    margin: ptr(10px);
    li {
      padding: ptr(10px);
      border-radius: ptr(4px);
      background-color: #fff;
      .order-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: ptr(10px);
        span {
          color: #666;
          i {
            vertical-align: middle;
          }
        }
        .order-status {
          color: red;
          font-size: ptr(14px);
        }
      }

      .goods-img {
        float: left;
        height: ptr(100px);
        width: ptr(150px);
        margin-right: ptr(10px);
        img {
          height: 100%;
          width: 100%;
          border-radius: ptr(4px);
        }
      }
      .goods-info {
        position: relative;
        height: ptr(100px);
        .goods-name {
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
          font-size: ptr(16px);
          color: #333;
          margin-bottom: ptr(10px);
        }
        .goods-message {
          font-size: ptr(14px);
          color: #666;
          margin-bottom: ptr(10px);
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .goods-price {
          position: absolute;
          bottom: 0;
          left: ptr(160px);
          color: #333;
          font-size: ptr(16px);
          width: ptr(175px);
          i {
            font-style: normal;
            font-size: ptr(12px);
          }
          .num {
            position: absolute;
            right: ptr(2px);
            color: #666;
            padding-top: ptr(4px);
            font-size: ptr(12px);
          }
        }
      }
    }
    .option {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: ptr(5px) ptr(20px);
      background-color: #fff;
      span {
        color: #333;
        font-size: ptr(14px);
      }
      .freight {
        padding-right: ptr(16px);
      }
    }
    .option:first-of-type {
      padding-top: ptr(10px);
    }
    .order-sum {
      text-align: right;
      padding-top: ptr(10px);
      font-size: ptr(12px);
      .price {
        font-weight: 600;
        font-size: ptr(14px);
      }
    }
    .upImg {
      float: right;
      padding-top: ptr(20px);
      .btn {
        height: ptr(30px);
        width: ptr(100px);
        line-height: ptr(30px);
        text-align: center;
        font-size: ptr(14px);
        color: #666;
        border-radius: ptr(30px);
        border: 1px solid #ccc;
      }
    }
  }
  .express,
  .shop {
    border-radius: ptr(4px);
    padding: ptr(10px);
    margin: ptr(5px) ptr(10px);
    background-color: #fff;
    h2 {
      font-size: ptr(14px);
      font-weight: 600;
      margin-bottom: ptr(10px);
    }
    section {
      color: #666;
      font-size: ptr(14px);
      margin-bottom: ptr(4px);
    }
    section span:last-child {
      color: #333;
    }
    .list li {
      margin-bottom: ptr(20px);
    }
    .nodata{
      text-align: center;
      color: #999;
      font-size: ptr(14px);
    }
    .location{
      padding-bottom: ptr(10px);
      color: #0cb0b5;
      font-size: ptr(16px);
      text-align: center;
    }
  }
}
.fill {
  height: ptr(60px);
  width: 100%;
}
</style>




