<template>
    <div >
        <div class="upload clearfix">
        <h2>上传病例</h2>
        <p>最少上传1张照片</p>
        <img
            :src="item.src"
            alt=""
            class="uploadImg"
            v-for="(item, index) in showImg"
            :key="index"
        >
        <section class="uploading-img" v-show="showImg.length<3">
            <span class="iconfont iconadd1"></span>
            <span>添加图片</span>
            <van-uploader :after-read="onRead">
                <van-icon name="photograph"/>
            </van-uploader>
        </section>
        </div>
       <div class="next" @click="save">完成</div>
        <div class="fill"></div>
    </div>
</template>
<script>
import base from "../assets/js/base.js";
import $ from 'jquery'
export default {
  name: "uploadCases",
  data() {
    return {
      showImg: []
    };
  },
  methods: {
    //上传图片
    onRead(files) {
      if(this.showImg.length==3){
         base.toast('最多上传3张图片');
         return;
      }
      let that = this;
      lrz(files.file, {
        quality: 0.1 //自定义使用压缩方式
      }).then(function(rst) {
          //成功时执行
          let file = new FormData();
          file.append("file", rst.base64.split(",")[1]);
          that.upImg(file);
        })
        .catch(function(error) {
          base.toast(error.msg);
        });
    },
    upImg(file) {
      let that = this;
      $.ajax({
        type: "post",
        url:
          "https://www.yizhenyun.com.cn/yzypt/upload/pictureUpload?medRecordNo=" +
          sessionStorage.getItem('medRecordNo'),
        data: file,
        dataType: "json",
        cache: false,
        processData: false,
        contentType: false,
        success: function(res) {
          base.toast(res.msg);
          let obj = {};
          obj.src = res.data;
          that.showImg = [...that.showImg, obj];
        },
        error: function(err) {
          base.toast(err.msg || "图片过大");
        }
      });
    },
    save() {
      if(this.showImg.length<1){
           base.toast('最少上传一张图片');
           return;
      }else{
        base.postRequest('/buy/saveMrPicture',{
          mallNo:sessionStorage.getItem('mallNo')
        }).then(res => {
          if (res.status == 0) {
            sessionStorage.removeItem('medRecordNo');
            sessionStorage.removeItem('mallNo');
             this.$router.push("order");
          } else {
            base.toast(res.msg);
          }
        });
      }
    },
  },
  created() {
 
  },
  mounted() {
    this.$store.commit('changeActive','hideFooter')
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.upload{
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
}
h2{
    font-size: ptr(16px);
    margin: ptr(10px) 0;
}
p {
  font-size: ptr(12px);
  color: #999;
  margin-bottom: ptr(40px);
}
.uploadImg {
  float: left;
  height: ptr(80px);
  width: ptr(80px);
  margin: 0 ptr(4px) ptr(4px) 0;
}
.uploading-img {
  float: left;
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: ptr(80px);
  width: ptr(80px);
  text-align: center;
  background-color: #eee;
  .iconfont {
    font-size: ptr(20px);
    margin-bottom: ptr(5px);
  }
  span {
    color: #999;
  }
  .imgFile {
    position: absolute;
    opacity: 0;
    height: ptr(80px);
    width: ptr(80px);
  }
}
.next {
  position: relative;
  top: ptr(44px);
  margin: 20px;
  padding: ptr(10px);
  text-align: center;
  font-size: ptr(16px);
  color: #fff;
  border-radius: ptr(4px);
  background-color: #0cb0b5;
}
.fill {
      position: relative;
  top: ptr(44px);
  height: ptr(60px);
  width: 100%;
}
</style>



