<template>
  <div>
    <header-bar>确认订单</header-bar>
    <main>
      <ul>
        <li class="clearfix">
          <div class="goods-img">
            <img :src="drugSet.titleImg" alt="">
          </div>
          <div class="goods-info">
            <div class="goods-name">{{drugSet.commonName}}</div>
            <div class="goods-message">{{drugSet.describe}}</div>
            <div class="goods-price">
              <i>￥</i>
              <span>{{drugSet.saleGenPrice}}</span>
              <span class="num">x{{goodsNum}}</span>
            </div>
          </div>
        </li>
      </ul>
      <section class="option">
        <span>配送方式</span>
        <span @click="isType=true">
          {{type}}
          <i class="iconfont iconright"></i>
        </span>
      </section>
      <section class="option">
        <span>运费</span>
        <span class="freight">{{freight}}</span>
      </section>
      <!--地址-->
      <div class="address" @click="open" v-show="addressShow">
        <div class="info">
          <section v-if="address && address.name">
            <span>{{address.name}}</span>
            <span>{{address.phone}}</span>
          </section>
          <section v-if="isNo">请添加地址</section>
          <div
            v-if="address"
            class="address-detail"
          >{{address.province}}{{address.city}}{{address.county}}{{address.addressDetail}}</div>
        </div>
        <div class="iconfont iconright"></div>
      </div>
      <div class="address-modal" v-if="isShow">
        <div class="bg" @click="close()"></div>
        <div class="content">
          <div class="list">
            <div
              class="info-list"
              :class="[item.mark=='1'?'active':'']"
              v-for="(item, index) in addressList"
              :key="index"
              @click="onSelect(item)"
            >
              <section>
                <span>{{item.name}}</span>
                <span>{{item.phone}}</span>
              </section>
              <div
                :class="[item.mark=='1'?'active':'address-detail']"
              >{{item.province}}{{item.city}}{{item.county}}{{item.addressDetail}}</div>
            </div>
            <div class="nodata" v-if="isNo">暂无地址</div>
          </div>
          <div class="add" @click="add()">新增</div>
        </div>
      </div>
      <div class="fill"></div>
    </main>
    <!--配送方式-->
    <van-popup v-model="isType" position="bottom" :style="{ height: '18%' }">
      <van-radio-group v-model="typeValue" @change="typeChange">
        <van-cell-group>
          <van-cell title="门店自提" clickable @click="typeValue = '门店自提'">
            <van-radio slot="right-icon" name="门店自提"/>
          </van-cell>
          <van-cell title="普通快递" clickable @click="typeValue = '普通快递'">
            <van-radio slot="right-icon" name="普通快递"/>
          </van-cell>
        </van-cell-group>
      </van-radio-group>
    </van-popup>
    <!--购买按钮-->
    <div class="buy-box">
      <div class="goods-num">共{{goodsNum}}件,</div>
      <div class="sum">
        合计:
        <i>￥</i>
        <span>{{sum}}</span>
      </div>
      <div class="btn" @click="pay">立即购买</div>
    </div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import headerBar from "@/components/header-bar";
import wx from "weixin-js-sdk";
export default {
  name: "affirmOrder",
  data() {
    return {
      isType: false,
      type: "门店自提",
      freight: 0,
      typeValue: "门店自提",
      isNo: true,
      addressList: [],
      isShow: false,
      sum: 0,
      address: {},
      drugSet: {},
      goodsNum: 1,
      addressShow: false
    };
  },
  methods: {
    pay() {
      let that = this;
      if(this.type == "普通快递" && this.addressList.length<=0){
        base.toast('请先添加地址');
        return;
      }
      base
        .postRequest("/buy/updateShippingStatus", {
          address:this.address.addId,
          mallNo: parseInt(sessionStorage.getItem("mallNo")),
          shippingStatus: this.type == "门店自提" ? 2 : 1
        })
        .then(res => {
          if (res.status == 0) {
            that.getParameter();
          } else {
            base.toast(res.msg);
          }
        });
    },
    //获取支付所需参数
    getParameter() {
      base
        .getAjax("/buy/wxpay", {
          mallNo: sessionStorage.getItem("mallNo")
        })
        .then(res => {
            this.weixinPay(res);
        });
    },
    //微信支付
    weixinPay(res) {
      let that = this;
      wx.config({
        debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
        appId: res.appid, // 必填，公众号的唯一标识
        timestamp: res.timestamp, // 必填，生成签名的时间戳
        nonceStr: res.nonceStr, // 必填，生成签名的随机串
        signature: res.signature, // 必填，签名
        jsApiList: ["chooseWXPay"] // 必填，需要使用的JS接口列表
      });
      wx.ready(function() {
        // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
        wx.chooseWXPay({
          timestamp: res.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
          nonceStr: res.nonceStr, // 支付签名随机串，不长于 32 位
          package: res.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
          signType: res.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
          paySign: res.signature, // 支付签名
          success: function() {
            that.setPayStatus()
          }
        });
      });
      wx.error(function() {
        // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
        that.pay();
      });
    },
    //获取地址
    getAdress() {
      base.getAjax("/ptInfo/selectReceiptAddress").then(res => {
        if (res.status == 0) {
          if (res.data.length > 0) {
            this.addressList = res.data;
            this.address = res.data.filter(ele => {
              return ele.mark == "1";
            })[0];
            this.isNo = false;
          } else {
            this.isNo = true;
          }
        } else {
          base.toast(res.msg);
        }
      });
    },
    onSelect(item) {
      let data = item;
      data.mark = 1;
      base
        .postAjax("/ptInfo/updateReceiptAddress", JSON.stringify(data))
        .then(res => {
          if (res.status == 0) {
            this.isShow = false;
            this.getAdress();
          } else {
            base.toast(res.msg);
          }
        });
    },
    open() {
      this.isShow = true;
    },
    close() {
      this.isShow = false;
    },
    add() { 
      this.$router.push("addAddress");
    },
    //修改支付状态
    setPayStatus(){
      base.postRequest('/buy/notice',{
      mallNo:parseInt(sessionStorage.getItem("mallNo"))
      }).then(res => {
          if (res.status == 0) {
            base.toast('支付成功');
            setTimeout(() => {
              this.$router.push("uploadCases");
            }, 1000); 
          } else {
            base.toast(res.msg);
          }
        });
    },
    //获取商品数量
    getData() {
      base
        .postRequest("/yzypt/selectMallOrder", {
          mallNo: parseInt(sessionStorage.getItem("mallNo"))
        })
        .then(res => {
          if (res.status == 0) {
            this.goodsNum = res.data.list[0].medItemQr[0].number;
            sessionStorage.setItem('medRecordNo',res.data.list[0].medRecord.medRecordNo)
            this.sum = base.accMul(this.goodsNum, this.price);
          } else {
            base.toast(res.msg);
          }
        });
    },
    //获取商品信息
    getGoods() {
      base
        .postRequest("/drugset/selectInfo", {
          drugSetNo: sessionStorage.getItem("drugSetNo")
        })
        .then(res => {
          if (res.status == 0) {
            this.drugSet = res.data.drugSet;
            this.price=res.data.drugSet.saleGenPrice;
            this.getData();
          } else {
            base.toast(res.msg);
          }
        });
    },
    typeChange(e) {
      this.type = e;
      if (e == "普通快递") {
        this.getAdress();
        this.freight = 0;
        this.addressShow = true;
        if (this.goodsNum < 3) {
          this.freight = 12;
        }
      } else {
        this.freight = 0;
        this.addressShow = false;
      }
      this.sum = base.accMul(this.goodsNum, this.price);
      this.sum = base.accAdd(parseFloat(this.sum), parseInt(this.freight));
      this.isType = false;
    }
  },
  components: {
    "header-bar": headerBar
  },
  created() {
    this.getGoods();
  },
  mounted() {
    this.$store.commit("changeActive", "hideFooter");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
main {
  position: relative;
  top: ptr(44px);
}
li {
  padding: ptr(10px);
  background-color: #fff;
  .goods-img {
    float: left;
    height: ptr(100px);
    width: ptr(150px);
    margin-right: ptr(10px);
    img {
      height: 100%;
      width: 100%;
      border-radius: ptr(4px);
    }
  }
  .goods-info {
    position: relative;
    height: ptr(100px);
    .goods-name {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      font-size: ptr(16px);
      color: #333;
      margin-bottom: ptr(10px);
    }
    .goods-message {
      font-size: ptr(14px);
      color: #666;
      margin-bottom: ptr(10px);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .goods-price {
      position: absolute;
      bottom: 0;
      left: ptr(160px);
      color: red;
      font-size: ptr(16px);
      width: ptr(190px);
      i {
        font-style: normal;
        font-size: ptr(12px);
      }
      .num {
        position: absolute;
        right: ptr(2px);
        color: #666;
        padding-top: ptr(4px);
        font-size: ptr(12px);
      }
    }
  }
}
.buy-box {
  position: fixed;
  bottom: ptr(-2px);
  left: 0;
  z-index: 3;
  height: ptr(60px);
  padding: 0 ptr(20px);
  width: 100%;
  box-sizing: border-box;
  background-color: #fff;
  text-align: right;
  box-shadow: 0 3px 14px 2px rgba(0, 0, 0, 0.12);
  .goods-num {
    display: inline-block;
    color: #666;
    font-size: ptr(14px);
  }
  .sum {
    display: inline-block;
    font-size: ptr(14px);
    margin-right: ptr(10px);
    i {
      color: red;
      font-style: normal;
      font-size: ptr(12px);
    }
    span {
      color: red;
      font-size: ptr(16px);
    }
  }
  .btn {
    display: inline-block;
    margin-top: ptr(10px);
    font-size: ptr(14px);
    padding: ptr(10px);
    color: #fff;
    border-radius: ptr(4px);
    text-align: center;
    background-color: red;
  }
}
.info-box {
  font-size: ptr(14px);
  margin-top: ptr(10px);
  padding: ptr(20px);
  background-color: #fff;
  li {
    margin-bottom: ptr(10px);
    div {
      display: inline-block;
    }
    .title {
      color: #666;
    }
    .val {
      color: #333;
    }
  }
  li:last-child {
    margin-bottom: 0;
  }
}
.option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
  span {
    color: #333;
    font-size: ptr(14px);
  }
  .freight {
    padding-right: ptr(16px);
  }
}
.option:first-of-type {
  padding-top: ptr(40px);
}
.address {
  margin-top: ptr(10px);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: ptr(10px) ptr(20px);
  background-color: #fff;
  border-bottom: 1px dotted #f4f4f4;
  .info {
    flex: 1;
    font-size: ptr(14px);
    span {
      margin-right: ptr(20px);
    }
    .address-detail {
      color: #666;
      margin-top: ptr(10px);
    }
  }
  .iconfont {
    font-size: ptr(20px);
    text-align: right;
    width: ptr(40px);
  }
}
.address-modal {
  .bg {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    height: 100%;
    width: 100%;
    background-color: rgba($color: #000000, $alpha: 0.5);
  }
  .content {
    position: fixed;
    top: 50%;
    left: 50%;
    z-index: 999;
    padding: ptr(10px);
    height: ptr(460px);
    width: ptr(300px);
    margin-left: ptr(-150px);
    margin-top: ptr(-230px);
    border-radius: ptr(8px);
    background-color: #fff;
    h2 {
      font-size: ptr(16px);
    }
    .list {
      height: ptr(400px);
      overflow: auto;
      .info-list {
        padding: ptr(10px);
        font-size: ptr(14px);
        border-bottom: 1px solid #f4f4f4;
        span {
          margin-right: ptr(20px);
        }
        .address-detail {
          color: #666;
          margin-top: ptr(5px);
        }
        .address-active {
          color: #0cb0b5;
          margin-top: ptr(5px);
        }
      }
      .nodata {
        text-align: center;
        color: #999;
        padding-top: ptr(30px);
        font-size: ptr(14px);
      }
    }
    .active {
      color: #0cb0b5;
    }
    .add {
      margin-top: ptr(20px);
      height: ptr(40px);
      line-height: ptr(40px);
      color: #0cb0b5;
      font-size: ptr(14px);
      text-align: center;
      border-radius: ptr(4px);
      border: 1px solid #0cb0b5;
    }
  }
}
.fill {
  height: ptr(60px);
  width: 100%;
}
</style>



