<template>
  <div>
    <header>
      <img :src="imgUrl" class="user" alt="头像">
      <div class="name">{{userinfo.name}}</div>
    </header>
    <main>
      <section @click="jumpPage('editMine')">
        <span>基础信息</span>
        <span class="iconfont iconright"></span>
      </section>
      <section @click="jumpPage('shippingAddress')">
        <span>收货地址</span>
        <span class="iconfont iconright"></span>
      </section>
    </main>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
export default {
  name: "mine",
  data() {
    return {
      imgUrl: require("../assets/img/user.jpg"),
      userinfo: {}
    };
  },
  methods: {
    jumpPage(url) {
      this.$router.push(url);
    },
    getData() {
      base.postRequest("/ptInfo/selectInfo").then(res => {
        if (res.status == 0) {
          this.userinfo = res.data;
          sessionStorage.setItem('userinfo',JSON.stringify(res.data))
        } else {
          base.toast(res.msg);
        }
      });
    }
  },
  created() {
    this.getData();
  },
  mounted() {
    this.$store.commit('changeActive','mine')
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
header {
  height: ptr(200px);
  padding: ptr(30px) ptr(20px);
  text-align: center;
  box-sizing: border-box;
  background-color: #0cb0b5;
  .name {
    font-size: ptr(16px);
    color: #fff;
    margin: ptr(20px);
  }
  .user {
    height: ptr(100px);
    width: ptr(100px);
    border-radius: 50%;
  }
}
main {
  section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: ptr(14px) ptr(20px);
    font-size: ptr(15px);
    color: #666;
    background-color: #fff;
    margin-bottom: ptr(2px);
    .iconfont {
      font-size: ptr(20px);
    }
  }
}
.fill {
  height: ptr(60px);
  width: 100%;
}
</style>



