import Vue from 'vue'
import Router from 'vue-router'
import EditMine from '@/pages/editMine'
import ShippingAddress from '@/pages/shippingAddress'
import EditAddress from '@/pages/editAddress'
import AddAddress from '@/pages/addAddress'

import Mine from '@/pages/mine'
import Home from '@/pages/home'
import Order from '@/pages/order'
import GoodsInfo from '@/pages/goodsInfo'
import AffirmOrder from '@/pages/affirmOrder'
import Distribution from '@/pages/distribution'
import UploadCases from '@/pages/uploadCases'
import OrderDetail from '@/pages/orderDetail'
import base from "../assets/js/base.js";
Vue.use(Router)


let router = new Router({
  routes: [
    {
      path: '/home',
      name: 'Home',
      meta: {
        title: "首页",
      },
      component: Home
    },
    {
      path: '/orderDetail',
      name: 'OrderDetail',
      meta: {
        title: "订单详情",
      },
      component: OrderDetail
    },
    {
      path: '/uploadCases',
      name: 'UploadCases',
      meta: {
        title: "上传病例",
      },
      component: UploadCases
    },
    {
      path: '/distribution',
      name: 'Distribution',
      meta: {
        title: "配送方式",
      },
      component: Distribution
    },
    {
      path: '/affirmOrder',
      name: 'AffirmOrder',
      meta: {
        title: "确认订单",
      },
      component: AffirmOrder
    },
    {
      path: '/goodsInfo',
      name: 'GoodsInfo',
      meta: {
        title: "商品详情",
      },
      component: GoodsInfo
    },
    {
      path: '/order',
      name: 'Order',
      meta: {
        title: "订单",
      },
      component: Order
    },
    {
      path:"/shippingAddress",
      name:"ShippingAddress",
      meta:{
        title:"收货地址",
      },
      component:ShippingAddress
    },
    {
      path:"/editAddress",
      name:"EditAddress",
      meta:{
        title:"编辑地址",
      },
      component:EditAddress
    },
    {
      path:"/addAddress",
      name:"AddAddress",
      meta:{
        title:"新增地址",
      },
      component:AddAddress
    },
    {
      path: '/mine',
      name: 'Mine',
      meta: {
        title: "我的",
      },
      component: Mine
    },
    {
      path: '/editMine',
      name: 'EditMine',
      meta: {
        title: "编辑信息",
      },
      component: EditMine
    },
  ]
});

router.afterEach((to, from, next) => {
  window.scrollTo(0, 0);
});

router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
});

export default router;


