<template>
    <div class="container">
        <section class="type">
            <span @click="onePick('0')" :class="[isIndex==0?'active':'']">门店自提</span>
            <span @click="expressage('1')" :class="[isIndex==1?'active':'']">快递配送</span>
        </section>
        <!--门店-->
        <ul class="info-box" v-show="isIndex==0">
            <li>
                <div class="title">门店名称:</div>
                <div class="val">天下第一点</div>
            </li>
            <li>
                <div class="title">门店地址:</div>
                <div class="val">浙江省杭州市拱墅区城市花园</div>
            </li>
            <li>
                <div class="title">门店电话:</div>
                <div class="val">13203987654</div>
            </li>
        </ul>
        <!--快递-->
        <ul class="info-box" v-show="isIndex==1">
            <li>
                <div class="title">姓名:</div>
                <div class="val">薛文诚</div>
            </li>
            <li>
                <div class="title">地址:</div>
                <div class="val">浙江省杭州市拱墅区城市花园</div>
            </li>
            <li>
                <div class="title">手机号:</div>
                <div class="val">13203987654</div>
            </li>
        </ul>
        <div class="next" @click="jumpPage">下一步</div>
        <div class="fill"></div>
        <!--门店列表-->
        <div class="shop-list-box" v-show="isShop">
            <div class="bg" @click="close()"></div>
            <ul class="list">
                <li v-for="(item, index) in 16" :key="index">
                    <div class="shop-name">天下第一点浙江省杭州市拱墅区城市花园</div>
                    <div class="shop-address">浙江省杭州市拱墅区城市花园浙江省杭州市拱墅区城市花园</div>
                </li>
                <div class="nodata" v-if="isNo">暂无数据</div>
            </ul>
        </div>
        <!--地址列表-->
        <div class="shop-list-box" v-show="isAddress">
            <div class="bg" @click="close()"></div>
            <ul class="list">
                <!-- <li v-for="(item, index) in 1" :key="index">
                    <div class="shop-name">薛文诚</div>
                    <div class="shop-address">浙江省杭州市拱墅区城市花园浙江省杭州市拱墅区城市花园</div>
                </li>-->
                <div class="add" v-if="isNo" @click="add">新增地址</div>
            </ul>
        </div>
    </div>
</template>
<script>
import base from "../assets/js/base.js";
export default {
  name: "distribution",
  data() {
    return {
      isIndex: null,
      isNo: true,
      isShop: false,
      isAddress: false
    };
  },
  methods: {
    jumpPage() {
      this.$router.push("uploadCases");
    },
    onePick(val) {
      this.isIndex = val;
      this.isShop = true;
    },
    expressage(val) {
      this.isIndex = val;
      this.isAddress = true;
    },
    close() {
      this.isShop = false;
      this.isAddress = false;
    },
    add() {
      this.$router.push("addAddress");
    },
    getData() {
      base.getAjax("/zlyyptbcjk/selectCpiAndPi").then(res => {
        if (res.status == 0) {
          this.userinfo = res.data;
          sessionStorage.setItem("userinfo", JSON.stringify(res.data));
        } else {
          base.toast(res.msg);
        }
      });
    }
  },
  created() {
    //this.getData();
  },
  mounted() {}
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.type {
  padding: ptr(30px) ptr(20px);
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  span {
    color: #333;
    font-size: ptr(14px);
    width: ptr(120px);
    height: ptr(40px);
    line-height: ptr(40px);
    text-align: center;
    border: 1px solid #ccc;
    border-radius: ptr(4px);
  }
}
.info-box {
  font-size: ptr(14px);
  margin-top: ptr(10px);
  padding: ptr(20px);
  background-color: #fff;
  li {
    margin-bottom: ptr(10px);
    div {
      display: inline-block;
    }
    .title {
      color: #666;
    }
    .val {
      color: #333;
    }
  }
  li:last-child {
    margin-bottom: 0;
  }
}
.active {
  color: #0cb0b5 !important;
  border-color: #0cb0b5 !important;
}
.next {
  margin: 20px auto;
  padding: ptr(10px);
  text-align: center;
  font-size: ptr(16px);
  width: ptr(100px);
  color: #fff;
  border-radius: ptr(4px);
  background-color: #0cb0b5;
}
.shop-list-box {
  .bg {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba($color: #000000, $alpha: 0.5);
  }
  .list {
    position: absolute;
    top: 50%;
    z-index: 3;
    left: 50%;
    margin-left: ptr(-150px);
    margin-top: ptr(-200px);
    width: ptr(300px);
    height: ptr(400px);
    background-color: #fff;
    border-radius: ptr(4px);
    padding: ptr(20px) ptr(10px);
    overflow: hidden;
    overflow-y: auto;
    box-sizing: border-box;
    li {
      margin-bottom: ptr(8px);
      padding-bottom: ptr(8px);
      border-bottom: 1px solid #ccc;
      div {
      }
      .shop-name {
        font-size: ptr(14px);
        color: #333;
        margin-bottom: ptr(5px);
      }
      .shop-address {
        font-size: ptr(12px);
        color: #666;
      }
    }
    .nodata {
      text-align: center;
      font-size: ptr(12px);
      color: #999;
    }
    .add {
      text-align: center;
      font-size: ptr(14px);
      padding: ptr(4px);
      border: 1px solid #0cb0b5;
      color: #0cb0b5;
      border-radius: ptr(4px);
    }
  }
}
.fill {
  height: ptr(60px);
  width: 100%;
}
</style>



