<template>
  <div>
    <ul>
      <li
        class="clearfix"
        @click="jumpPage(item.drugSet.drugSetNo,item.goods.goodsNo)"
        v-for="(item, index) in goodsList"
        :key="index"
      >
        <div class="goods-img">
          <img :src="item.drugSet.titleImg" alt="">
        </div>
        <div class="goods-info">
          <div class="goods-name">{{item.drugSet.commonName}}</div>
          <div class="goods-message">{{item.drugSet.describe}}</div>
          <div class="goods-price">
            <i>￥</i>
            {{item.drugSet.saleGenPrice}}
          </div>
        </div>
      </li>
    </ul>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
export default {
  name: "home",
  data() {
    return {
      goodsList: []
    };
  },
  methods: {
    jumpPage(drugSetNo, goodsNo) {
      sessionStorage.setItem("drugSetNo", drugSetNo);
      sessionStorage.setItem("goodsNo", goodsNo);
      setTimeout(() => {
        this.$router.push("goodsInfo");
      }, 100);
    },
    getData() {
      base
        .postRequest("/drugset/select", {
          version: 2
        })
        .then(res => {
          if (res.status == 0) {
            this.goodsList = res.data.list;
          } else {
            base.toast(res.msg);
          }
        });
    }
  },
  created() {
    this.getData();
  },
  mounted() {
    this.$store.commit("changeActive", "home");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
li {
  padding: ptr(10px);
  background-color: #fff;
  .goods-img {
    float: left;
    height: ptr(100px);
    width: ptr(150px);
    margin-right: ptr(10px);
    img {
      height: 100%;
      width: 100%;
      border-radius: ptr(4px);
    }
  }
  .goods-info {
    position: relative;
    height: ptr(100px);
    .goods-name {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      font-weight: 600;
      font-size: ptr(16px);
      color: #333;
      margin-bottom: ptr(10px);
    }
    .goods-message {
      font-size: ptr(14px);
      color: #666;
      margin-bottom: ptr(10px);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .goods-price {
      position: absolute;
      bottom: 0;
      left: ptr(160px);
      color: red;
      font-size: ptr(16px);
      font-weight: 600;
      i {
        font-style: normal;
        font-weight: normal;
        font-size: ptr(12px);
      }
    }
  }
}
.fill {
  height: ptr(60px);
  width: 100%;
}
</style>



