<template>
  <div :class="[isQuestion?'forbidScroll':'']">
    <header-bar>商品详情</header-bar>
    <main>
      <div class="header-img">
        <img :src="drugSet.titleImg" alt="">
      </div>
      <div class="goods-info">
        <div class="goods-name">{{drugSet.commonName}}</div>
        <div class="price-number clearfix">
          <div class="goods-price">
            <i>￥</i>
            {{drugSet.saleGenPrice}}
          </div>
          <div class="number">
            <i class="iconfont iconjianshao" @click="minus"></i>
            <span>{{goodsNum}}</span>
            <i class="iconfont iconadd1" @click="add"></i>
          </div>
        </div>
      </div>
      <div class="goods-parameter">
        <h2>商品参数</h2>
        <ul class="goods-p-i">
          <li>商品名称：{{drugSet.itemName}}</li>
          <li>通用名称：{{drugSet.commonName}}</li>
          <li>商品规格：{{drugSet.dosege}}{{drugSet.dosegeUnit}}*{{drugSet.divisor}}{{drugSet.doseUnit}}/{{drugSet.saleUnit}}</li>
          <li>生产企业：{{drugSet.manufactory}}</li>
          <li>有效期：{{drugSet.validity}}</li>
          <li>批准文字：{{drugSet.approvalNumber}}</li>
        </ul>
      </div>
      <div class="goods-parameter">
        <h2>图文详情</h2>
        <ul class="goods-p-i">
          <img :src="item" alt="" v-for="(item, index) in drugSet.detailsImg" :key="index">
        </ul>
      </div>
      <!--问卷-->
      <div class="question" v-if="isQuestion">
        <div class="bg"></div>
        <div class="swiper">
          <div class="test" v-for="(item, index) in testArr" :key="index">
            <div class="one-title h2">{{index+1}}、{{item.questionText}}</div>
            <template v-if="item.questionType=='2' || item.questionType=='3'">
              <!--选择题-->
              <div class="option" v-for="(ite, inde) in item.list" :key="inde">
                <div class="box" @click="isSelect(index,inde)">
                  <i
                    class="iconfont"
                    :class="[ite.checked?'iconright-f active':'iconcircle2yuanquan']"
                  ></i>
                  <div>{{ite.options}}</div>
                </div>
              </div>
            </template>
            <template v-else>
              <!--填空题-->
              <van-field
                type="number"
                class="gap-fill"
                maxlength="50"
                placeholder="请填写数字"
                v-model="item.note"
                @input="fillInput('number')"
                v-if="item.isNumber==0"
              />
              <van-field
                class="gap-fill"
                maxlength="50"
                placeholder="请填写"
                v-model="item.note"
                @input="fillInput('')"
                v-else
              />
            </template>
          </div>
          <div class="submit" @click="submitQuestinon">提交</div>
        </div>
      </div>
      <!--欣比克-->
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克成分</h2>
        <ul class="goods-p-i">
          <li>本品活性成分为本维莫德</li>
          <li>化学名称：5-[(E)-2 苯乙烯基]-2-异丙基-1,3-苯二酚</li>
          <li>分子式：C17H18O2 分子量:254.32</li>
          <li>辅料：十六醇、白凡士林、轻质液体石蜡、单、双硬脂酸甘油酯、丙七醇、聚山梨酯80、聚苯乙酯和纯化水。</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克性状</h2>
        <ul class="goods-p-i">
          <li>本品为类白色乳膏</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克适应症</h2>
        <ul class="goods-p-i">
          <li>本品用于适合局部治疗的成人轻中度寻常型银屑病</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克规格</h2>
        <ul class="goods-p-i">
          <li>10g：0.1g(1％)</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克用法用量</h2>
        <ul class="goods-p-i">
          <li>皮肤局部外用，每日两次，早晚各一次， 均匀涂抹于患处，形成一薄层即可。每日最大使用剂量不超过6g，治疗面积不应超过体表面积的10％。患处皮肤涂布本品后严禁日光照射，在自然光照下也需注意采取避光措施</li>
          <li>本品连续使用超过12周的安全有效性尚来确立。临床用药总时间最长不得超过12周，本品停药后重复使用的安全有效性尚未确立，不推荐重复使用</li>
          <li>本品不可用于头面部、口周及眼睑部、腹股沟、肛门生殖器等部位。用药后请立即洗手</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克不良反应</h2>
        <ul class="goods-p-i">
          <li>详情见说明书</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克禁忌</h2>
        <ul class="goods-p-i">
          <li>1、对本维莫德或乳育中其他任何成分过敏者禁用</li>
          <li>2、妊娠、计划妊娠及哺乳期妇女禁用</li>
          <li>3、点滴状，红皮病型，关节病型和脓瘤型银屑病患者禁用</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克注意事项</h2>
        <ul class="goods-p-i">
          <li>详情见说明书</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克孕妇及哺乳期妇女用药</h2>
        <ul class="goods-p-i">
          <li>孕妇:本品尚未在孕妇中进行临床试验，孕妇用药的安全性尚未确立</li>
          <li>动物实验结果显示，以30mg/kg/日的剂量对大鼠和以6mg/kg/日的剂量对免皮下注射本品时，会产生胚胎减少和骨骼发育迟缓等生殖毒性。因此，妊娠和计划妊板的妇女禁止使用本品</li>
          <li>哺乳期妇女:本品尚未在哺乳明妇女中进行临床试验，哺乳明妇女用药的安全性尚未确立</li>
          <li>动物实验结果显示，本品会通过乳汁分泌。因此，哺乳期妇女应慎用本品，只有在充分权衡且治疗获益明显大于风险的前提下，方可考虑使用本品，使用本品时应停止哺乳</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克儿童用药</h2>
        <ul class="goods-p-i">
          <li>尚未在18岁以下患者中进行本维莫德乳膏治疗银屑病的有效性和安全性研究，因此，18 岁以下患者不得使用本品</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克老年用药</h2>
        <ul class="goods-p-i">
          <li>本维莫德乳膏临床研究未包括足够数量的65岁及以上的受试者，不能确定65岁及以上惠者与年轻患者是否有不同的反应</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克药物相互作用</h2>
        <ul class="goods-p-i">
          <li>尚不明确。未进行该项试验且无可靠参考文献</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克药物过量</h2>
        <ul class="goods-p-i">
          <li>尚不明确。未进行该项试验且无可靠参考文献。请严格按照说明书推岸的用法和用量使用</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克贮藏</h2>
        <ul class="goods-p-i">
          <li>遮光，密封，阴凉处(不超过20°C)保存。请置于儿童不易触及之处</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克执行标准</h2>
        <ul class="goods-p-i">
          <li>YBH01282019</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2>欣比克核准日期</h2>
        <ul class="goods-p-i">
          <li>2019年5月29日</li>
        </ul>
      </div>
      <div class="fill"></div>
    </main>
    <!--购买按钮-->
    <div class="buy-box">
      <div class="btn" @click="submit">立即购买</div>
    </div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import headerBar from "@/components/header-bar";
import { Dialog } from "vant";
export default {
  name: "goodsInfo",
  data() {
    return {
      goodsNum: 1,
      drugSet: {},
      goods: {},
      testArr: [],
      isQuestion: false
    };
  },
  methods: {
    submit() {
      base
        .postRequest("/buy/insertGoods", {
          goodsNo: sessionStorage.getItem("goodsNo"),
          number: this.goodsNum
        })
        .then(res => {
          if (res.status == 0) {
            sessionStorage.setItem("mallNo", res.data.mallNo);
            setTimeout(() => {
              this.$router.push("affirmOrder");
            }, 100);
          } else {
            base.toast(res.msg);
          }
        });
    },
    minus() {
      if (this.goodsNum > 1) {
        this.goodsNum--;
      } else {
        this.goodsNum = 1;
        base.toast("数量最少为一个");
      }
    },
    isSelect(i, j) {
      //单选
      if (this.testArr[i].questionType == 2) {
        for (let s = 0; s < this.testArr[i].list.length; s++) {
          this.testArr[i].list[s].checked = false;
        }
        this.testArr[i].list[j].checked = true;
      }
      //多选
      if (this.testArr[i].questionType == 3) {
        this.testArr[i].list[j].checked = !this.testArr[i].list[j].checked;
      }
    },
    //填空输入
    fillInput(type, e) {
      var patrn = /^\d+(\.\d+)?$/;
      if (this.fillIndex != null && this.fillIndex2 != null) {
        //填空
        if (type == "number") {
          if (patrn.test(e.detail.value)) {
            this.testArr[this.fillIndex].note = e.detail.value;
          } else {
            this.toast("请输入数字");
            this.testArr[this.fillIndex].note = "";
            return;
          }
        } else {
          this.testArr[this.fillIndex].note = e.detail.value;
        }
      }
    },
    //提交问卷
    submitQuestinon() {
      let that = this;
      let userQuestion = [];
      //总题数
      for (let i = 0; i < this.testArr.length; i++) {
        var obj = {};
        obj.id = null;
        obj.ptNo = null;
        obj.templateNo = this.testArr[i].templateNo;
        obj.questionNo = this.testArr[i].questionNo;
        //判断题型
        //填空
        if (this.testArr[i].questionType == 1) {
          obj.options = this.testArr[i].note;
          if (this.testArr[i].isNull == 1 && !this.testArr[i].note) {
            base.toast("答题不完整");
            return;
          }
        } else {
          //多选
          if (this.testArr[i].questionType == 3) {
            let answerArr = this.testArr[i].list.filter(ele => {
              return ele.checked == true;
            });
            if (answerArr.length > 0) {
              let arr = [];
              for (let i = 0; i < answerArr.length; i++) {
                arr.push(answerArr[i].options);
              }

              obj.options = arr.join();
            } else {
              if (this.testArr[i].isNull == 1) {
                base.toast("答题不完整");
                return;
              }
            }
            //单选
          } else if (this.testArr[i].questionType == 2) {
            let answerArr = this.testArr[i].list.filter(ele => {
              return ele.checked == true;
            });
            if (answerArr.length > 0) {
              obj.options = answerArr[0].options;
            } else {
              if (this.testArr[i].isNull == 1) {
                base.toast("答题不完整");
                return;
              }
            }
          }
        }
        userQuestion.push(obj);
      }
      if (
        this.goods.goodsNo == 9999 &&
        (userQuestion[0].options != "是" || userQuestion[1].options != "是")
      ) {
        Dialog.alert({
          title: "提示",
          message: "请到医院或者专科门诊确诊后再进行自助下单"
        }).then(() => {
          this.$router.push("home");
        });
        return;
      } else {
        this.submitFun(userQuestion);
      }
    },
    submitFun(e) {
      let data = {
        userQuestion: e
      };
      base.postAjax("/buy/insertQuestion", JSON.stringify(data)).then(res => {
        if (res.status == 0) {
          this.isQuestion = false;
          base.openScroll();
        } else {
          base.toast(res.msg);
          this.isQuestion = true;
        }
      });
    },
    add() {
      this.goodsNum++;
    },
    //获取商品信息
    getData() {
      base
        .postRequest("/drugset/selectInfo", {
          drugSetNo: sessionStorage.getItem("drugSetNo")
        })
        .then(res => {
          if (res.status == 0) {
            this.drugSet = res.data.drugSet;
            this.goods = res.data.goods;
          } else {
            base.toast(res.msg);
          }
        });
    },
    //获取问卷
    getQuestion() {
      base
        .postRequest("/buy/selectQuestion", {
          goodsNo: sessionStorage.getItem("goodsNo")
        })
        .then(res => {
          if (res.status == 0) {
            let arr = [];
            for (let i = 0; i < res.data.questionTemplates.length; i++) {
              let options = [];
              for (let j = 0; j < res.data.questionOptions.length; j++) {
                if (
                  res.data.questionTemplates[i].templateNo ==
                    res.data.questionOptions[j].templateNo &&
                  res.data.questionTemplates[i].questionNo ==
                    res.data.questionOptions[j].questionNo
                ) {
                  res.data.questionOptions[j].checked = false;
                  options.push(res.data.questionOptions[j]);
                  res.data.questionTemplates[i].list = options;
                }
              }
            }
            this.testArr = JSON.parse(
              JSON.stringify(res.data.questionTemplates)
            );
          } else {
            base.toast(res.msg);
          }
        });
    },
    //判断问卷答题情况
    isAnswer() {
      base.forbiddenScroll();
      base
        .postRequest("/buy/isNullQuestion", {
          goodsNo: sessionStorage.getItem("goodsNo")
        })
        .then(res => {
          if (res.status == 0) {
            if (res.data == 0) {
              this.getQuestion();
              this.isQuestion = true;
            } else {
              this.isQuestion = false;
              base.openScroll();
            }
          } else {
            base.toast(res.msg);
          }
        });
    }
  },
  components: {
    "header-bar": headerBar
  },
  created() {
    this.isAnswer();
    this.getData();
  },
  mounted() {
    this.$store.commit("changeActive", "hideFooter");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.forbidScroll {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
main {
  position: relative;
  top: ptr(44px);
}
.header-img {
  height: ptr(200px);
  padding: ptr(5px) ptr(20px);
  background-color: #fff;
  img {
    width: 100%;
    height: 100%;
  }
}
.goods-info {
  padding: ptr(20px);
  background-color: #fff;
  margin-bottom: ptr(10px);
  .goods-name {
    font-size: ptr(16px);
    margin-bottom: ptr(10px);
  }
  .price-number {
    .goods-price {
      color: red;
      font-size: ptr(16px);
      i {
        font-size: ptr(12px);
        font-style: normal;
      }
    }
    .number {
      float: right;
      font-size: ptr(16px);
      span {
        display: inline-block;
        min-width: ptr(50px);
        text-align: center;
      }
      i {
        color: #666;
        font-size: ptr(20px);
      }
    }
  }
}
.goods-parameter {
  padding: ptr(20px);
  background-color: #fff;
  border-bottom: 1px solid #eee;
  h2 {
    color: #333;
    font-size: ptr(16px);
    margin-bottom: ptr(10px);
  }
  .goods-p-i {
    li {
      color: #666;
      margin-bottom: ptr(8px);
    }
    img {
      height: auto;
      width: 100%;
      display: block;
    }
  }
}
.buy-box {
  position: fixed;
  bottom: ptr(-2px);
  left: 0;
  z-index: 3;
  height: ptr(60px);
  padding: 0 ptr(20px);
  width: 100%;
  box-sizing: border-box;
  background-color: #fff;
  box-shadow: 0 3px 14px 2px rgba(0, 0, 0, 0.12);
  .btn {
    margin-top: ptr(10px);
    font-size: ptr(14px);
    padding: ptr(10px);
    color: #0cb0b5;
    border: 1px solid #0cb0b5;
    border-radius: ptr(4px);
    text-align: center;
  }
}
.question {
  .bg {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 4;
    height: 100%;
    width: 100%;
    background-color: rgba($color: #000000, $alpha: 0.5);
  }
}
.swiper {
  position: absolute;
  background-color: #fff;
  left: 50%;
  top: 0;
  width: ptr(300px);
  height: ptr(500px);
  margin-left: ptr(-150px);
  z-index: 5;
  transition: all 0.2s ease;
  overflow-y: auto;
  border-radius: ptr(4px);
  .test {
    padding: ptr(20px) ptr(20px) 0;
    box-sizing: border-box;
    .h2 {
      font-size: ptr(16px);
    }
    .h3 {
      margin-top: ptr(5px);
      font-size: ptr(15px);
    }
    .one-title {
      color: #333;
    }
    .two-title {
      color: #999;
      margin-left: ptr(23px);
    }

    .three-title {
      color: #555;
      margin: ptr(5px) ptr(20px) 0;
    }

    .option {
      font-size: ptr(15px);
      color: #777;
      margin: 0 ptr(30px);
      .box {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        padding: ptr(10px) 0;
        .iconfont {
          font-size: ptr(20px);
        }
        div {
          flex: 1;
          margin-left: ptr(4px);
        }
      }
      .active {
        color: #0cb0b5;
      }
    }
    .gap-fill {
      margin: ptr(10px) 0 ptr(5px) ptr(30px);
      padding: ptr(5px);
      font-size: ptr(14px);
      border: 1px solid #eee;
      border-radius: ptr(4px);
      outline: none;
      width: ptr(200px) !important;
    }
    .gap-none {
      display: none;
    }
  }
  .submit {
    margin: ptr(20px);
    padding: ptr(8px) 0;
    color: #fff;
    font-size: ptr(14px);
    text-align: center;
    border-radius: ptr(4px);
    background-color: #0cb0b5;
  }
}
.fill {
  height: ptr(60px);
  width: 100%;
}
</style>



