<template>
  <div id="app">
    <router-view/>
    <footer-bar v-show="isFooter"></footer-bar>
  </div>
</template>

<script>
import footerBar from "./components/footer-bar";
export default {
  name: "App",
  data() {
    return {
      isFooter: true
    };
  },
  watch: {
    footerValue(val) {
      if(val=='hideFooter'){
        this.isFooter=false;
      }else{
        this.isFooter=true;
        this.active = val;
      }
    }
  },
  computed: {
    footerValue() {
      return this.$store.state.active;
    }
  },
  components: {
    "footer-bar": footerBar
  }
};
</script>

<style lang="scss">
@import "./assets/scss/base.scss";
#app {
  height: 100%;
}
</style>



