<template>
       <header>
         <i class="iconfont iconleft1" @click="goBack()"></i><span><slot></slot></span>
       </header>
</template>
<script>
export default { 
  name: "header-bar",
  methods:{
    goBack(){
      window.history.go(-1);
    } 
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  display: flex;
  width: 100%;
  align-items: center;
  height: ptr(44px);
  padding-left: ptr(10px);
  color: #fff;
  background-color: #0CB0B5;
  font-size: ptr(16px);
  i {
    font-size: ptr(20px);
  }
  span {
    margin-left: ptr(10px);
  }
}
</style>





