<template>
  <div>
    <header>
      <img :src="imgUrl" class="user" alt="头像">
    </header>
    <main>
      <section class="box">
        <span class="title">姓名</span>
        <span class="value">
          <input type="text" placeholder="请输入姓名" v-model.trim="userinfo.name">
        </span>
      </section>
      <section class="box">
        <span class="title">性别</span>
        <span class="value" @click="showBtn(0)">{{userinfo.sex}}</span>
        <van-popup v-model="sexShow" position="bottom">
          <van-picker
            show-toolbar
            :columns="columns"
            @change="onChange"
            @confirm="sexConfirm"
            @cancel="close"
          />
        </van-popup>
      </section>
      <section class="box">
        <span class="title">身份证</span>
        <span class="value">
          <input type="text" placeholder="请输入身份证号" v-model.trim="userinfo.idNo">
        </span>
      </section>
      <section class="box">
        <span class="title">生日</span>
        <span class="value" @click="showBtn(1)">{{userinfo.birthDay}}</span>
        <van-popup v-model="birthDayShow" position="bottom">
          <van-datetime-picker
            v-model="birthDay"
            type="date"
            @confirm="birthDayConfirm"
            @cancel="close"
            :min-date="new Date(1911,0,1)"
          />
        </van-popup>
      </section>
      <section class="box">
        <span class="title">年龄</span>
        <span class="value">{{age}}</span>
      </section>
      <section class="box">
        <span class="title">手机号</span>
        <span class="value">
          <input
            type="number"
            placeholder="请输入手机号"
            v-model.trim="userinfo.phone"
            @input="phoneInput"
          >
        </span>
      </section>
      <section class="box">
        <span class="title">验证码</span>
        <span class="value">
          <input type="number" class="code" placeholder="请输入验证码" v-model.trim="code">
          <span
            class="getCode"
            :class="[isActive?'active':'getCode']"
            @click="getCode()"
          >{{codeValue}}</span>
        </span>
      </section>
      <section class="box">
        <span class="title">身高</span>
        <span class="value">
          <input
            type="number"
            placeholder="请输入身高(CM)"
            v-model.trim="userinfo.height"
            @input="heightInput"
            @blur="heightBlur"
          >
        </span>
      </section>
      <section class="box">
        <span class="title">体重</span>
        <span class="value">
          <input
            type="number"
            placeholder="请输入体重(KG)"
            v-model.trim="userinfo.weight"
            @input="weightInput"
            @blur="weightBlur"
          >
        </span>
      </section>
    </main>
    <div class="button" @click="save()">保存</div>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
export default {
  name: "editMine",
  data() {
    return {
      imgUrl: require("../assets/img/user.jpg"),
      columns: ["男", "女"],
      sex: "男",
      sexShow: false,
      birthDayShow: false,
      birthDay: "",
      phone: "",
      age: "",
      name: "",
      height: "",
      weight: "",
      code: "",
      userinfo: {},
      isActive: false,
      codeValue: "获取"
    };
  },
  methods: {
    onChange(picker, value, index) {
      this.sex = value;
    },
    showBtn(val) {
      switch (val) {
        case 0:
          this.sexShow = true;
          break;
        case 1:
          this.birthDayShow = true;
          break;
      }
    },
    /**获取验证码 */
    getCode() {
      if (!base.isPoneAvailable(this.userinfo.phone)) {
        base.toast("手机号格式不正确");
        return;
      }
      if (this.codeValue == "获取" && this.userinfo.phone) {
        this.getCodeFun();
        this.isActive = true;
        this.codeValue = 60;
        var setSecond = setInterval(() => {
          if (this.codeValue <= 0) {
            clearInterval(setSecond);
            this.codeValue = "获取";
            this.isActive = false;
          } else {
            this.codeValue--;
          }
        }, 1000);
      }
    },
    getCodeFun() {
      base
        .postRequest("/yzypt/sednSms", { phone: this.userinfo.phone })
        .then(res => {
          if (res.status != 0) {
            base.toast(res.msg);
            return;
          }
        });
    },
    sexConfirm(val) {
      this.userinfo.sex = val;
      this.close();
    },
    birthDayConfirm(val) {
      this.userinfo.birthDay = base.timeSwitch2(val, false);
      this.ageFun();
      this.close();
    },
    phoneInput() {
      if (this.userinfo.phone.length > 11) {
        base.toast("手机号码最多11位");
        this.userinfo.phone = this.userinfo.phone.substring(0, 11);
      }
    },
    heightBlur() {
      if (this.userinfo.height > 230) {
        base.toast("身高异常");
        this.userinfo.height = "";
      }
    },
    weightBlur() {
      if (this.userinfo.weight > 300) {
        base.toast("体重异常");
        this.userinfo.weight = "";
      }
    },
    heightInput() {
      if (this.userinfo.height.length > 4) {
        base.toast("身高参数有误");
        this.userinfo.height = "";
      }
    },
    weightInput() {
      if (this.userinfo.weight.length > 4) {
        base.toast("体重参数有误");
        this.userinfo.weight = "";
      }
    },
    save() {
      if (this.userinfo.phone && this.userinfo.height && this.userinfo.weight) {
        if (!base.isCardVerify(this.userinfo.idNo)) {
          base.toast("身份证号码格式错误");
          return false;
        }
        if (!base.isPoneAvailable(this.userinfo.phone)) {
          base.toast("手机号码格式错误");
          return false;
        }
        if (!this.code) {
          base.toast("请输入验证码");
          return false;
        }
        let data = {
          ptNo: this.userinfo.ptNo,
          name: this.userinfo.name,
          sex: this.userinfo.sex,
          idNo: this.userinfo.idNo,
          birthDay: this.userinfo.birthDay,
          phone: this.userinfo.phone,
          code: this.code,
          height: this.userinfo.height,
          weight: this.userinfo.weight
        };
        base.postRequest("/ptInfo/ptInfoUpdate", data).then(res => {
          if (res.status == 0) {
            base.toast(res.msg);
            sessionStorage.setItem("userinfo", JSON.stringify(data));
            setTimeout(() => {
              this.$router.push("mine");
            }, 500);
          } else {
            base.toast(res.msg);
          }
        });
      } else {
        base.toast("信息不完整");
      }
    },
    close() {
      this.sexShow = false;
      this.birthDayShow = false;
    },
    //年龄推算
    ageFun() {
      if (this.userinfo.birthDay) {
        this.age = base.sumAge(this.userinfo.birthDay);
      }
    }
  },
  created() {
    let userinfo = JSON.parse(sessionStorage.getItem("userinfo"));
    this.userinfo = userinfo;
    if (userinfo.birthDay) {
      this.userinfo.birthDay = base.timeSwitch(userinfo.birthDay, false);
      this.birthDay = base.timeSwitch(userinfo.birthDay, false);
    } else {
      this.userinfo.birthDay = base.timeSwitch(new Date(), false);
      this.birthDay = base.timeSwitch(new Date(), false);
    }
    this.ageFun();
  },
  mounted() {
    this.$store.commit("changeActive", "mine");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
header {
  height: ptr(200px);
  padding: ptr(30px) ptr(20px);
  text-align: center;
  box-sizing: border-box;
  background-color: #0cb0b5;
  .name {
    font-size: ptr(16px);
    color: #fff;
    margin: ptr(20px);
  }
  .user {
    height: ptr(100px);
    width: ptr(100px);
    border-radius: 50%;
  }
}
main {
  section {
    display: flex;
    align-items: center;
    padding: ptr(10px) ptr(20px);
    font-size: ptr(16px);
    height: ptr(30px);
    background-color: #fff;
    margin-bottom: ptr(2px);
    .title {
      width: ptr(80px);
      margin-right: ptr(10px);
    }
    .value {
      flex: 1;
      height: 100%;
      font-size: ptr(16px);
      line-height: ptr(30px);
      color: #666;
      input {
        font-size: ptr(16px);
        width: 100%;
        height: 100%;
        border: none;
        outline: none;
      }
      .code {
        width: ptr(100px);
      }
      .getCode {
        display: inline-block;
        width: ptr(50px);
        height: ptr(28px);
        color: #fff;
        font-size: ptr(14px);
        line-height: ptr(28px);
        border-radius: ptr(4px);
        text-align: center;
        background-color: #0cb0b5;
        margin-right: ptr(20px);
      }
      .active {
        width: ptr(50px);
        height: ptr(28px);
        color: #666;
        line-height: ptr(28px);
        border-radius: ptr(4px);
        text-align: center;
        background-color: #eee;
        margin-right: ptr(20px);
      }
    }
  }
}
.button {
  background-color: #0cb0b5;
  color: #fff;
  width: ptr(200px);
  margin: ptr(20px) auto;
  border-radius: ptr(5px);
  text-align: center;
  height: ptr(40px);
  font-size: ptr(16px);
  line-height: ptr(40px);
}
.fill {
  height: ptr(60px);
  width: 100%;
}
</style>



