<template>
  <div>
    <header>
      <ul class="clearfix">
        <li
          v-for="(item, index) in menu"
          :key="index"
          :class="[item.check?'active':'']"
          @click="selectMenu(index)"
        >{{item.title}}</li>
      </ul>
    </header>
    <main>
      <ul class="order-box" v-for="(item, index) in orderList" :key="index">
        <li class="clearfix" @click="orderDetail(item)">
          <section class="order-header">
            <span>
              <i class="iconfont iconweibiaoti-"></i>
              {{item.mallOrder.medOrderNo}}
            </span>
            <span
              class="order-status"
              v-if="item.mallOrder.orderStatus==0 && item.mallOrder.shippingStatus==0"
            >已配送</span>
            <span
              class="order-status"
              v-else-if="item.mallOrder.orderStatus==0 && item.mallOrder.shippingStatus==3"
            >已自提</span>
            <span
              class="order-status"
              v-else-if="item.mallOrder.orderStatus==1 && item.mallOrder.shippingStatus==1 && item.mrPictures.length>0"
            >未配送</span>
            <span
              class="order-status"
              v-else-if="item.mallOrder.orderStatus==1 && item.mallOrder.shippingStatus==2"
            >未自提</span>
            <span
              class="order-status"
              v-else-if="item.mallOrder.orderStatus==1 && item.mrPictures.length<=0"
            >未上传病例</span>
          </section>
          <div class="goods-img">
            <img :src="item.medItemQr[0].drugSet.titleImg" alt="">
          </div>
          <div class="goods-info">
            <div class="goods-name">{{item.medItemQr[0].drugSet.commonName}}</div>
            <div class="goods-message">{{item.medItemQr[0].drugSet.describe}}</div>
            <div class="goods-price">
              <i>￥</i>
              <span>{{item.medItemQr[0].saleGenPrice}}</span>
              <span class="num">x{{item.medItemQr[0].number}}</span>
            </div>
          </div>
          <section class="option">
            <span>配送方式</span>
            <span>{{item.mallOrder.shippingStatus | shippingFilter}}</span>
          </section>
          <section class="option">
            <span>运费</span>
            <span
              class="freight"
              v-if="(item.mallOrder.shippingStatus==1 || item.mallOrder.shippingStatus==0) && item.medItemQr[0].number<3"
            >12</span>
            <span class="freight" v-else>0</span>
          </section>
          <section class="order-sum">
            <span>共{{item.medItemQr[0].number}}件商品 合计:￥</span>
            <span class="price">{{item.mallOrder.orderAmount}}</span>
          </section>
          <div class="upImg" v-if="item.mrPictures.length<=0">
            <p class="btn" @click.stop="upImg(item.medRecord.medRecordNo,item.mallOrder.mallNo)">上传病例</p>
          </div>
        </li>
      </ul>
      <div class="nodata" v-show="isNo">暂无订单</div>
    </main>
    <div class="fill"></div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
export default {
  name: "order",
  data() {
    return {
      orderStatus: 2,
      orderList: [],
      isNo: false,
      menu: [
        { title: "全部", check: true, value: 2 },
        { title: "未完成", check: false, value: 1 },
        { title: "已完成", check: false, value: 0 }
      ]
    };
  },
  methods: {
    orderDetail(obj) {
      sessionStorage.setItem("orderInfo", JSON.stringify(obj));
      setTimeout(() => {
        this.$router.push("orderDetail");
      }, 100);
    },
    upImg(medRecordNo,mallNo) {
      sessionStorage.setItem("mallNo", mallNo);
      sessionStorage.setItem("medRecordNo", medRecordNo);
      setTimeout(() => {
        this.$router.push("uploadCases");
      }, 100);
    },
    getData() {
      this.orderList = [];
      base
        .postRequest("/ptInfo/findMallOrder", {
          orderStatus: this.orderStatus
        })
        .then(res => {
          if (res.status == 0) {
            this.orderList = res.data;
            if (res.data.length <= 0) {
              this.isNo = true;
            } else {
              this.isNo = false;
            }
          } else {
            base.toast(res.msg);
          }
        });
    },
    selectMenu(j) {
      this.orderStatus = this.menu[j].value;
      for (let i = 0; i < this.menu.length; i++) {
        this.menu[i].check = false;
      }
      this.menu[j].check = true;
      this.getData();
    },
  },
  filters: {
    shippingFilter(e) {
      if (e == 1 || e==0) {
        return "普通快递";
      } else {
        return "门店自提";
      }
    }
  },
  created() {
    this.getData();
  },
  mounted() {
    this.$store.commit("changeActive", "order");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
header {
  padding: ptr(10px) 0;
  li {
    position: relative;
    padding: ptr(5px) 0;
    float: left;
    width: 33%;
    font-size: ptr(14px);
    color: #666;
    text-align: center;
  }
  .active {
    color: #0cb0b5;
    // border-bottom: 1px solid #0cb0b5;
  }
}
.order-box {
  margin: ptr(5px) ptr(10px);
  li {
    padding: ptr(10px);
    border-radius: ptr(4px);
    background-color: #fff;
    .order-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: ptr(10px);
      span {
        color: #666;
        i {
          vertical-align: middle;
        }
      }
      .order-status {
        color: red;
        font-size: ptr(14px);
      }
    }

    .goods-img {
      float: left;
      height: ptr(100px);
      width: ptr(150px);
      margin-right: ptr(10px);
      img {
        height: 100%;
        width: 100%;
        border-radius: ptr(4px);
      }
    }
    .goods-info {
      position: relative;
      height: ptr(100px);
      .goods-name {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        font-size: ptr(16px);
        color: #333;
        margin-bottom: ptr(10px);
      }
      .goods-message {
        font-size: ptr(14px);
        color: #666;
        margin-bottom: ptr(10px);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .goods-price {
        position: absolute;
        bottom: 0;
        left: ptr(160px);
        color: #333;
        font-size: ptr(16px);
        width: ptr(175px);
        i {
          font-style: normal;
          font-size: ptr(12px);
        }
        .num {
          position: absolute;
          right: ptr(2px);
          color: #666;
          padding-top: ptr(4px);
          font-size: ptr(12px);
        }
      }
    }
  }
  .option {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: ptr(20px) ptr(20px) 0;
    background-color: #fff;
    span {
      color: #333;
      font-size: ptr(14px);
    }
    .freight {
      padding-right: ptr(16px);
    }
  }
  .order-sum {
    text-align: right;
    padding-top: ptr(10px);
    font-size: ptr(12px);
    .price {
      font-weight: 600;
      font-size: ptr(14px);
    }
  }
  .upImg {
    float: right;
    padding-top: ptr(20px);
    .btn {
      height: ptr(30px);
      width: ptr(100px);
      line-height: ptr(30px);
      text-align: center;
      font-size: ptr(14px);
      color: #666;
      border-radius: ptr(30px);
      border: 1px solid #ccc;
    }
  }
}
.nodata {
  text-align: center;
  color: #999;
  font-size: ptr(14px);
  padding-top: ptr(20px);
}
.fill {
  height: ptr(60px);
  width: 100%;
}
</style>



