<template>
    <div>
        <header-bar>新增地址</header-bar>
        <van-address-edit
            :area-list="areaList"
            show-postal
            show-set-default
            show-search-result
            :search-result="searchResult"
            @save="onSave"
            @change-detail="onChangeDetail"
        />
    </div>
</template>
<script>
import base from "../assets/js/base.js";
import areaList from "../assets/js/area.js";
import headerBar from "@/components/header-bar";
export default {
  name: "addAddress",
  data() {
    return {
      areaList: areaList,
      searchResult: []
    };
  },
  methods: {
    onSave(content) {
       console.log(content)
      let data={
          name:content.name,
          phone:content.tel,
          postalCode:content.postalCode,
          province:content.province,
          city:content.city,
          areaCode:content.areaCode,
          county:content.county,
          addressDetail: content.addressDetail,
          disableFlag: 0,
          mark: content.isDefault?'1':'0',
      };
      sessionStorage.setItem('addressObj',JSON.stringify(data));
      base.postAjax("/ptInfo/insertReceiptAddress", JSON.stringify(data))
        .then(res => {
          if (res.status == 0) {
            base.toast(res.msg);
            setTimeout(() => {
                this.$router.go(-1)
            }, 1000);
          } else {
            base.toast(res.msg);
          }
        });
    },
    onChangeDetail(item){

    }
  },
  components: {
    "header-bar": headerBar
  },
    mounted() {
    this.$store.commit('changeActive','mine')
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.van-address-edit {
  position: relative;
  top: ptr(44px);
}
</style>



