

import { Toast } from 'vant';
import $ from 'jquery';
export default {
  /***禁止滚动***/
  forbiddenScroll() {
    document.querySelector("body").style.overflowY = "hidden";
  },
  /***开启滚动***/
  openScroll() {
    document.querySelector("body").style.overflowY = "auto";
  },
  /**轻提示 */
  toast(val) {
    Toast({
      duration: 1000,
      message: val
    });
  },
  loading() {
    Toast.loading({
      mask: false,
      message: "获取中...",
      duration: 0,
      forbidClick: true, // 禁用背景点击
      loadingType: 'spinner'
    });
  },
  closeLoading() {
    Toast.clear()
  },
  /***修改tabbar***/
  changeTabbar(val) {
    let arr = document.querySelectorAll('footer a');
    for (let i = 0; i < arr.length; i++) {
      arr[i].style.color = "#949494";
    }
    document.querySelector(val).style.color = "#0CB0B5";
  },
  /***时间转换***/
  timeSwitch(val, judge) {
    if (val) {
      let value = val.toString().substring(0, 10);
      let date = new Date(value.replace(/\-/g, "/"));
      let Month = date.getMonth() + 1;
      let day = date.getDate();
      let hours = date.getHours();
      let minutes = date.getMinutes();
      let second = date.getSeconds();
      if (judge) {
        return date.getFullYear() + '-' + this.isTwo(Month) + '-' + this.isTwo(day) + ' ' + this.isTwo(hours) + ':' + this.isTwo(minutes) + ':' + this.isTwo(second);
      } else {
        return date.getFullYear() + '-' + this.isTwo(Month) + '-' + this.isTwo(day)
      }
    }

  },
  timeSwitch2(val, judge) {
    let date = new Date(val);
    let Month = date.getMonth() + 1;
    let day = date.getDate();
    let hours = date.getHours();
    let minutes = date.getMinutes();
    if (judge) {
      return date.getFullYear() + '-' + this.isTwo(Month) + '-' + this.isTwo(day) + ' ' + this.isTwo(hours) + ':' + this.isTwo(minutes)
    } else {
      return date.getFullYear() + '-' + this.isTwo(Month) + '-' + this.isTwo(day)
    }
  },
  isTwo(val) {
    if (val.toString().length > 1) {
      return val;
    } else {
      return '0' + val;
    }
  },
  /***手机号验证***/
  isPoneAvailable(val) {
    var myreg = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/;
    if (myreg.test(val)) {
      return true;
    } else {
      return false;
    }
  },
  /***姓名验证***/
  isNameVerify(name) {
    var myReg = /^(?:[\u4e00-\u9fa5]+)(?:●[\u4e00-\u9fa5]+)*$|^[a-zA-Z0-9]+\s?[\.·\-()a-zA-Z]*[a-zA-Z]+$/;
    if (!myReg.test(name)) {
      return false;
    } else {
      return true;
    }
  },
  /**身份证 */
  isCardVerify(card) {
    var myReg = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/;
    if (!myReg.test(card)) {
      return false;
    } else {
      return true;
    }
  },
  /***get请求***/
  getAjax(url, dataObj) {
    return new Promise((resolve, reject) => {
      $.ajax({
        type: "get",
        url: 'https://www.yizhenyun.com.cn/yzypt' + url,
        data: dataObj,
        success: function (res) {
          resolve(res)
        },
        error: function (err) {
          Toast({
            duration: 1000,
            message: err.msg
          });
        }
      })
    })
  },
  /***post请求***/
  postAjax(url, data) {
    return new Promise((resolve, reject) => {
      $.ajax({
        type: "POST",
        contentType: "application/json;charset=utf-8",
        url: 'https://www.yizhenyun.com.cn/yzypt' + url,
        data: data,
        success: function (res) {
          resolve(res)
        },
        error: function (err) {
          Toast({
            duration: 1000,
            message: err.msg
          });
        }
      })
    })
  },
  postRequest(url, data) {
    return new Promise((resolve, reject) => {
      $.ajax({
        type: "POST",
        url: 'https://www.yizhenyun.com.cn/yzypt' + url,
        data: data,
        success: function (res) {
          resolve(res)
        },
        error: function (err) {
          Toast({
            duration: 1000,
            message: err.msg
          });
        }
      })
    })
  },

  /***滚动高度***/
  getScrollTop() {
    var scrollTop = 0;
    if (document.documentElement && document.documentElement.scrollTop) {
      scrollTop = document.documentElement.scrollTop;
    }
    else if (document.body) {
      scrollTop = document.body.scrollTop;
    }
    return scrollTop;
  },
  /***可视窗口高度***/
  getClientHeight() {
    var clientHeight = 0;
    if (document.body.clientHeight && document.documentElement.clientHeight) {
      var clientHeight = (document.body.clientHeight < document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight;
    }
    else {
      var clientHeight = (document.body.clientHeight > document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight;
    }
    return clientHeight;
  },

  /***文档高度***/
  getDocumentHeight() {
    return Math.max(document.body.scrollHeight, document.documentElement.scrollHeight);
  },
  //计算年龄
  sumAge(value) {
    if(value){
      let currentYear = new Date().getFullYear();
      let currentMon = new Date().getMonth() + 1;
      let currentDate = new Date().getDate();
      let birthYear = new Date(value).getFullYear();
      let birthMon = new Date(value).getMonth() + 1;
      let birthDate = new Date(value).getDate();
      let differ = currentYear - birthYear;
      if (birthMon > currentMon) {
        differ--;
      } else if (birthMon == currentMon) {
        if (birthDate > currentDate) {
          differ--;
        }
      }
      return differ;
    }else{
      return 0;
    }

  },
  //乘法 
  accMul(arg1, arg2) {
    var m = 0, s1 = arg1.toString(), s2 = arg2.toString();
    try { m += s1.split(".")[1].length } catch (e) { }
    try { m += s2.split(".")[1].length } catch (e) { }
    return Number(s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m)
  },
  //加法
  accAdd(arg1, arg2) {
    var r1, r2, m;
    try { r1 = arg1.toString().split(".")[1].length } catch (e) { r1 = 0 }
    try { r2 = arg2.toString().split(".")[1].length } catch (e) { r2 = 0 }
    m = Math.pow(10, Math.max(r1, r2))
    return (arg1 * m + arg2 * m) / m
  }
}



