<template>
  <div>
    <header>
      <i class="iconfont iconleft1" @click="goBack()"></i>
      <span>收货地址</span>
    </header>
    <van-address-list
      v-model="chosenAddressId"
      :list="list"
      @add="onAdd"
      @edit="onEdit"
      @select="onSelect"
    />
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import headerBar from "@/components/header-bar";
export default {
  name: "shippingAddress",
  data() {
    return {
      chosenAddressId: "1",
      list: []
    };
  },
  methods: {
    goBack() {
      this.$router.push("mine");
    },
    onAdd() {
      this.$router.push("addAddress");
    },
    onEdit(item, index) {
      sessionStorage.setItem("addressObj", JSON.stringify(item));
      this.$router.push("editAddress");
    },
    onSelect(item, index) {
      let data = item;
      data.mark = 1;
      base
        .postAjax("/ptInfo/updateReceiptAddress", JSON.stringify(data))
        .then(res => {
          if (res.status == 0) {
          } else {
            base.toast(res.msg);
          }
        });
    },
    getData() {
      base.getAjax("/ptInfo/selectReceiptAddress").then(res => {
        if (res.status == 0) {
          for (let i = 0; i < res.data.length; i++) {
            res.data[i].tel = res.data[i].phone;
            res.data[i].address =
              res.data[i].province + res.data[i].city + res.data[i].county + res.data[i].addressDetail;
            if (res.data[i].mark == 1) {
              res.data[i].id = "1";
            }
          }
          this.list = res.data;
        } else {
          base.toast(res.msg);
        }
      });
    }
  },
  created() {
    this.getData();
  },
  mounted() {
    this.$store.commit("changeActive", "mine");
  },
  components: {
    "header-bar": headerBar
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  display: flex;
  width: 100%;
  align-items: center;
  height: ptr(44px);
  padding-left: ptr(10px);
  color: #fff;
  background-color: #0cb0b5;
  font-size: ptr(16px);
  i {
    font-size: ptr(20px);
  }
  span {
    margin-left: ptr(10px);
  }
}
.van-address-list {
  position: relative;
  top: ptr(44px);
}
</style>



