<template>
  <div>
    <header-bar>编辑地址</header-bar>
    <van-address-edit
      :area-list="areaList"
      :address-info="addressInfo"
      show-postal
      show-delete
      show-set-default
      show-search-result
      @save="onSave"
      @delete="onDelete"
    />
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import areaList from "../assets/js/area.js";
import headerBar from "@/components/header-bar";
export default {
  name: "editAddress",
  data() {
    return {
      areaList: areaList,
      addressInfo: {}
    };
  },
  methods: {
    onSave(e) {
      let data = {
         addId:e.addId,
          name:e.name,
          phone:e.tel,
          postalCode:e.postalCode,
          province:e.province,
          city:e.city,
          areaCode:e.areaCode,
          county:e.county,
          addressDetail: e.addressDetail,
          disableFlag: 0,
          mark: e.isDefault?'1':'0',
          ptNo: e.ptNo
      };
      base
        .postAjax("/ptInfo/updateReceiptAddress", JSON.stringify(data))
        .then(res => {
          if (res.status == 0) {
            base.toast('修改成功');
            setTimeout(() => {
              this.$router.push("shippingAddress");
            }, 1000);
          } else {
            base.toast(res.msg);
          }
        });
    },
    onDelete(item) {
      base
        .postRequest("/ptInfo/deleteReceiptAddress", { addId: item.addId })
        .then(res => {
          if (res.status == 0) {
            base.toast(res.msg);
            setTimeout(() => {
              this.$router.push("shippingAddress");
            }, 1000);
          } else {
            base.toast(res.msg);
          }
        });
    }
  },
  created() {
    let addressInfo = JSON.parse(sessionStorage.getItem("addressObj"));
    this.addressInfo = addressInfo;
    this.addressInfo.isDefault = addressInfo.mark == 1 ? true : false;
  },
  components: {
    "header-bar": headerBar
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.van-address-edit {
  position: relative;
  top: ptr(44px);
}
</style>



