import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
    state: {
      active:""
    },
    mutations: {
      changeActive(state,value){
        state.active=value;
      }
    },
  })
export default store;


